const TermsContainer = () => {
    return (
        <>
            <Navbar />
            <div className="content-container">
            <div className="guide-field">
            <div className="container">
        <h1 className="fs-md-32 text-heading lh-141 mb-6 mxw-751 text-center">
          IU KHON V QUY NH CHUNG  THU NH</h1>
        <div className="mxw-751">
          <div className="lh-214 mb-9">
            <p style={{textAlign: 'left'}} align="center"><span ><strong>1.&nbsp;</strong><strong>TRNG THI BAN U &amp; KT THC CHO THU</strong></span></p>
            <p>Vo thi im bt u ca Hp ng cho thu, Cn h s c giao cho v c chp nhn bi Bn thu trong tnh trng hin ti ca Cn h.</p>
            <p>Bn thu phi bn giao Cn h cho Bn cho thu vo thi im kt thc Hp ng trong tnh trng nh lc bt u thi im thu nh, ngoi tr nhng sa cha c tin hnh bi Bn cho thu v nhng xung cp thng thng trong qu trnh thu nh.</p>
            <p><span >&nbsp;<strong>2.&nbsp;</strong><strong>S DNG NH CHO THU</strong>&nbsp;</span></p>
            <p><strong>2.1</strong>&nbsp;Cn h ny s c Bn Thu s dng ch  lm ni  cho c nhn hoc h gia nh. Bn Thu khng c s dng ton b hoc bt l phn no ca Cn h ny vo bt k thi im no trong thi hn ca Hp ng thu nh ny cho mc ch tin hnh bt k hot ng kinh doanh hoc ngh nghip thuc bt k loi hnh no, hoc bt c hot ng bt hp php no i vi Lut Vit Nam, hoc cho bt k mc ch no m khng phi l lm ni  cho c nhn hoc h gia nh.&nbsp;</p>
            <p><strong>2.2</strong>&nbsp;Bt c vic sa cha hoc ci to no nhm mc ch s dng ring ca Bn Thu phi c chp thun bng vn bn ca Bn Cho Thu v phi tun theo nhng qui nh v xy dng ca Lut php Vit Nam.&nbsp;</p>
            <p><strong>2.3</strong>&nbsp;Bn Thu phi tun th mi quy nh php lut v v sinh, y t, phng chy cha chy, v sinh mi trng, trt t an ninh ca cc c quan Nh nc c thm quyn v hip hi ch nh/ch chung c, nu c, lin quan n Cn h.</p>
            <p><span >&nbsp;<strong>3.&nbsp;</strong><strong>S NGI C NG</strong><strong>&nbsp;</strong></span></p>
            <p><strong>3.1.</strong> Bn Thu ng  rng s ngi c ng thng xuyn ti Cn h s khng vt qu s ngi c quy nh nh tha thun trong Hp ng cho thu m khng c s chp thun trc bng vn bn ca Bn Cho Thu.&nbsp;</p>
            <p><strong>3.2.</strong> Cung cp bn ph t H Chiu v Th Thc (ca cc thnh vin trong gia nh) cho Bn Cho Thu  thc hin vic ng k tm tr vo ngy khch chuyn vo cn h. Nu Bn Thu khng cung cp  ng k vi Cng An khu vc th Bn Thu s phi chu trch nhim trc Php Lut Vit Nam.&nbsp;</p>
            <p><strong>3.3.</strong> Trong trng hp c khch n thm Bn Thu ti Cn h v tm tr li, Bn Thu phi thng bo cho Bn Cho Thu v danh tnh v thi gian lu tr, cung cp visa, h chiu hoc giy t c nhn ca khch cho Bn Cho Thu. Bn Cho Thu khng c t chi chp thun mt cch bt hp l v vic ny.&nbsp;</p>
            <p><strong>3.4.</strong> Bn Cho Thu c trch nhim xin cp v/hoc duy tr Giy php cho ngi nc ngoi thu nh, theo qui nh ca lut php Vit Nam, i vi thi hn thu nh v gia hn thu nh theo hp ng thu nh. C trch nhim thc hin vic ng k tm tr cho Bn Thu theo ng quy nh v cung cp cho Bn Thu nhng chng nhn lin quan.</p>
            <p><span  >&nbsp;<strong>4.</strong>&nbsp;<strong>GIA HN</strong></span></p>
            <p><strong>4.1.</strong> Bn Thu c quyn la chn gia hn Thi hn ca Hp ng thu nh ny bng cch thng bo cho Bn Cho Thu  nh gia hn ca mnh t nht 30 (ba mi) ngy trc Ngy ht hn.</p>
            <p><strong>4.2.</strong> Nu Bn thu khng gia hn hp ng ng thi hn, Hp ng thu nh v mi dch v lin quan n cn h s b dng. (h thng in, th t...)&nbsp; &nbsp; &nbsp; &nbsp;&nbsp;</p>
            <p><span  ><strong>&nbsp;5.&nbsp;</strong><strong>DCH V TRONG CN H</strong>&nbsp;</span></p>
            <p><strong>5.1.</strong> Vn  v internet, bn thu c s dng internet min ph do Bn Cho Thu cung cp&nbsp; vi tc  ti a cam kt l: 12 MB/s</p>
            <p><strong>5.2.</strong> Dch v truyn hnh cp cng c cung cp cho bn thu vi 65 knh pht sng, chi tit c th tham kho ti ng link: <a href="http://www.vtvcab.vn/dich-vu/truyen-hinh-cap/vtvcab-tai-ha-noi/bang-kenh-analog">http://www.vtvcab.vn/dich-vu/truyen-hinh-cap/vtvcab-tai-ha-noi/bang-kenh-analog</a>&nbsp;</p>
            <p><strong>5.3.</strong> Dch v lau dn, git l (3 ln/tun) v ph s dng nc sinh hot ch p dng cho 2 ngi . Nu c pht sinh thm ngi th 3 vi thi hn  Cn h l 01 thng tr ln th s tnh ph ph thu nh thng tin chi tit c ghi di y:</p>
            <ul>
              <li>Dch v lau dn v git l: $50/ngi/thng (Tng ng 1,00,000 VND)</li>
              <li>Nc sinh hot: $10/ngi/thng (Tng ng 200,000 VND)</li>
            </ul>
            <p><strong><em>&nbsp;</em>5.4.</strong> Mi cn h s c cp ti a 2 b cha kha. Trong trng hp Bn Thu lm mt hay cn cp thm th s phi tr thm chi ph ph thu: 100,000 VND/b&nbsp;</p>
            <p><strong>5.5.</strong> Tin in p dng cho nhng cn h dng chung my git, my sy s l VND 200,000/thng/cn h.&nbsp;</p>
            <p><strong>5.6.</strong> Phc v n sng ti cn h (Nu c)&nbsp;</p>
            <p><strong>5.7.</strong> a n ra sn bay Quc t Ni Bi (Nu c)&nbsp;</p>
            <p><strong>5.8.</strong> Truyn hnh cp quc t v truyn hnh Nht NHK (Theo tha thun)&nbsp;</p>
            <p><strong>5.9.</strong> Bo tr v bo dng nh k (Chi tit nh km theo danh sch cc Quy nh)</p>
            <p><span  ><em>&nbsp;</em><strong>6.</strong><strong>&nbsp;PHNG THC THANH TON</strong><strong>&nbsp;</strong></span></p>
            <p>Bn Thu c ngha v tr tin thu, ha n in v cc chi ph khc lin quan ng hn nh quy nh trong Hp ng cho thu.&nbsp;</p>
            <p><span  ><strong>&nbsp;7.&nbsp;</strong><strong>CHUYN NHNG V CHO THU LI</strong>&nbsp;</span></p>
            <p><strong>7.1.</strong> Bn Cho Thu khng c chuyn nhng hp ng thu ny hoc khng c cho thu li Cn H cho nhng ngi khc v bt c l do g trong thi hn Hp ng thu nh ny cn hiu lc.&nbsp;</p>
            <p><strong>7.2.</strong> Bt k vic chuyn nhng hoc cho thu li m khng c s ng  trc bng vn bn ca Bn Cho Thu s b v hiu ha v cu thnh hnh vi vi phm ngha v ca Bn Thu theo iu ny.</p>
            <p>&nbsp;<span  ><strong>8.&nbsp;</strong><strong>THAY I, BO TR V SA CHA</strong>&nbsp;</span></p>
            <p><strong>8.1.</strong> Bn Thu khng c thay i Cn h hoc xy dng bt k kt cu hoc thc hin nhng sa sang khc m khng c s ng  trc bng vn bn ca Bn Cho Thu s b cho l vi phm ngha v ca Bn Thu theo iu ny.&nbsp;</p>
            <p><strong>8.2.</strong> Tt c cc sa cha c thc hin trong vng 1 tun k t ngy nhn c yu cu t Bn Thu. Nu khng Bn Thu s c quyn sa cha nhng h hng ny m khng thng bo thm cho Bn Cho Thu. Nu Bn Thu sa cha nhng h hng ny th Bn Thu s yu cu Bn Cho Thu thanh ton chi ph sa cha hoc t ng khu tr vo Tin thu phi tr<em><br /></em></p>
            <p><strong>8.3.</strong> Vi nhng cn h khng c ngi lau dn,  m bo an ton cho ngi , ch nh s c k hoch vo kim tra v bo dng cc thit b in ca cn h. Lch kim tra s c thng bo trc cho khch hng.&nbsp;</p>
            <p><span  ><strong>&nbsp;9.&nbsp;</strong><strong>TH NUI</strong><strong>&nbsp;</strong></span></p>
            <p>Trong trng hp khch c nui ch, mo trong cn h, Bn Thu s phi tun th nhng quy nh nh sau:</p>
            <p><strong>9.1. </strong>Phi cung cp y  giy chng nhn v tim chng ch mo&nbsp;</p>
            <p><strong>9.2.</strong> Trong trng hp ch, mo khng cn  trong cn h na, bn thu phi dn dp sch s&nbsp; khu vc ngoi cn h v c nhng khu vc lin quan pha trong cn h.&nbsp;</p>
            <p><strong>9.3.</strong> Khng c  ch, mo i ra ngoi m khng c s gim st hoc bo v ng cch.  gim thiu nguy him c th gy ra cho nhng ngi thu nh khc, phi eo bo v cho ch, mo khi chng i ra ngoi.&nbsp;</p>
            <p><strong>9.4.</strong> Bn thu s phi chu trch nhim n b nu c bt c h hi g v thit b hay cn h m gy ra bi ch mo&nbsp;</p>
            <p><strong>&nbsp;</strong><strong>10.&nbsp;</strong><strong>QUYN VO NH CHO THU</strong>&nbsp;</p>
            <p>Bn Cho Thu v/hoc i din ca Bn Cho Thu ch c quyn vo Cn h sau khi c s cho php t Bn thu v thi gian,ngy thng, cho mc ch:&nbsp;</p>
            <p>&nbsp; &nbsp; &nbsp; &nbsp;<strong> &nbsp;i.</strong> Kim tra Cn h v/hoc thc hin vic sa cha thay th i vi Cn h hoc cc hng mc trong Cn h nh quy nh theo Hp ng thu nh ny.&nbsp;</p>
            <p>&nbsp; &nbsp;<strong> &nbsp; &nbsp; &nbsp;ii.</strong> a ngi thu nh khc xem Cn h trong thi gian 30 (ba mi) ngy cui cng ca Hp ng thu nh ny; hoc&nbsp;</p>
            <p>&nbsp; &nbsp;<strong> &nbsp; &nbsp; &nbsp;iii.</strong> khc phc tnh trng khn cp.</p>
            <p><span  >&nbsp;<strong>11.&nbsp;</strong><strong>AN NINH V BO V TI SN</strong></span></p>
            <p><strong>&nbsp;11.1. </strong>Lin quan n vn  an ninh: Bn Cho Thu khng chu trch nhim theo st trong vic m bo an ton cho Bn Thu. Bn Thu s phi chu trch nhim v s an ton ca ti sn thuc quyn s hu ca h. Tt c ca s v ca chnh nn c ng, kha cn thn. Bt c ai gh thm phi c kim tra cn thn trc khi cho vo cn h. C gng hn ch khng i ra ngoi cng vin, bi  xe mt mnh, c bit sau khi tri ti. Nu c bt c s vic ng nghi ng no xung quanh cn h, hy gi cho Bn Cho Thu hoc bo v ta nh ngay lp tc.&nbsp;</p>
            <p><strong>11.2.</strong> Vic bo v ti sn ca Bn Thu (nh l: kt st, tin mt, trang sc) hon ton l trch nhim ca Bn Thu.</p>
            <p><strong>11.3.</strong> Bn cho thu s khng chu trch nhim vi bt c hng hc, mt mt g v  c c nhn ca Bn thu hoc cc thit b khc trong khu vc cho thu m gy ra bi Bn thu hoc bn th 3.</p>
            <p><strong>11.4.</strong> Mi hnh vi t chc nh bc, mi dm u b nghim cm theo php lut Vit Nam.&nbsp;</p>
            <p><span  ><strong>&nbsp;12.&nbsp;</strong><strong>CHM DT HP NG</strong></span></p>
            <p>&nbsp;Hp ng thu ny c chm dt trong nhng trng hp sau:</p>
            <p><strong>12.1.</strong> Trong trng hp Bn Thu b trc xut hoc b thu hi giy php hot ng ti Vit Nam hoc ngi c ng ca cn nh thuyn chuyn cng tc ra nc ngoi. Bn thu phi thng bo trc cho Bn Cho Thu 30 ngy bng vn bn. Bn Cho Thu s hon tr li Bn Thu y  ton b s tin t cc v khon tin thu nh  tr trc cn li khng s dng.<em>&nbsp;</em></p>
            <p><strong>12.2.</strong> Trong trng hp nh cho thu b tho d theo quyt nh ca cc cp c thm quyn hoc b chy (khng do li ca Bn Thu) hoc giy php thu nh cho ngi nc ngoi khng th gia hn. Bn Cho Thu s hon tr li Bn Thu y  ton b s tin t cc v khon tin thu nh  tr trc cn li khng s dng.<em>&nbsp;</em></p>
            <p><strong>12.3.</strong> Trng hp Bt Kh Khng: Thin tai, l lt, chin tranh, ting n t cng trng xy dng hoc mi trng xung quanh, Bn Cho Thu s hon tr li Bn Thu y  ton b s tin t cc v khon tin thu nh  tr trc cn li khng s dng t Bn Thu. Trong trng hp ny, Bn Thu c th n phng chm dt hp ng m khng b pht, tuy nhin phi gi cho Bn Cho Thu thng bo trc 01 thng.&nbsp;</p>
            <p><strong>12.4.</strong> Trong trng hp ting n t cng trnh xy dng hoc mi trng xung quanh nh hng xu ti cuc sng ca Bn Thu  trong Cn h, th Bn Thu c th chm dt Hp ng cho thu nh m khng b pht, tuy nhin phi gi cho Bn Cho Thu thng bo trc 01 thng. Bn Cho Thu s hon tr li Bn Thu y  ton b s tin t cc v khon tin thu nh  tr trc cn li khng s dng t Bn Thu.</p>
            <p>&nbsp;<span  ><strong>13.&nbsp;</strong><strong>TING N TRONG CN H</strong></span>&nbsp;</p>
            <p>Quy nh v ting n trong cn h: mi thit b m thanh nh: TV, i pht thanh, thit b nghe pht nhc m c th gy ra ting n phi c iu chnh m thanh ph hp  khng gy nh hng n nhng ngi thu khc. Khng t chc chi nhc theo nhm trong cn h vo bt c thi gian no.</p>
            <p><span  >&nbsp;<strong>14.&nbsp;</strong><strong>S DNG KHU VC CHUNG</strong></span></p>
            <p>Trong trng hp, Bn thu c nhu cu t chc tic  snh ta nh, sn thng hoc s dng khu vc ny v mc ch c nhn, Bn thu phi hi  kin ca Bn cho thu trc v tr ph s dng nh quy nh. Phi thng bo trc cho Bn cho thu.  Bn cho thu c th thng bo cho cc Bn thu khc.&nbsp;</p>
            </div>
        </div>
      </div>
      </div>
            </div>
            <Footer />
        </>
    )
}

export default TermsContainer
